section {
    padding: 50px 0;

    .inner {
        width: 1200px;
        margin: 0 auto;

        h2 {
            font-size: 23px;
            font-weight: 700;
            text-align: center;
        }
    }
}

.btn {
    padding: 30px 0 0 0;
    text-align: right;

    button {
        margin: 0 0 0 auto;
        border: 1px solid #ced4da;
        padding: 7px 20px;
        background: #f8f9fa;
        font-size: 15px;
        border-radius: 3px;
        cursor: pointer;
    }
}

.BoardList {
    h3 {
        text-align: center;
        font-size: 17px;
    }

    input,
    textarea {
        width: 100%;
    }

    textarea {
        min-height: 150px;
    }

    table {
        margin: 30px 0 0 0;
        border-top: 2px solid #000;
        width: 100%;
        border-spacing: 0;
        border-collapse: collapse;

        tr {
            border-bottom: 1px solid #ddd;

            th {
                padding: 15px 0;
                font-size: 17px;
                font-weight: 500;
                border-bottom: 1px solid #000;
                background: #f7f7f7;
            }

            td {
                padding: 10px 0;
                font-size: 15px;
                line-height: 1.4;
                text-align: center;
            }

            .tit {
                padding: 0 0 0 30px;
                text-align: left;
            }
        }
    }
}